.places{
  padding: 5em 0 3em;
  .container{
    padding: 0 1.875em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5em;
  }
  .h2{
    max-width: 17em;
  }
  &__wood{
    width: 15.75em;
  }
  &__grid{
    margin-top: 5.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 6.25em / 3));
    gap: 3em 3.125em;
  }
}

.reviews{
  padding: 2.75em 0;
  .container{
    padding: 0 5.625em;
  }
  &__inner{
    position: relative;
  }
  &__top{
    position: absolute;
    left: 0;
    top: 0;
    max-width: 32em;
  }
}

.reviews-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 0;
      display: flex;
    }
    &-button{
      position: static;
      margin: 0;
      width: 6.25em;
      height: 6.25em;
      background-color: $white;
      @include center;
      img{
        width: 3.25em;
      }
    }
  }
}

.review-card{
  height: 100%;
  display: grid;
  grid-template-columns: 38.75em calc(100% - 38.75em);
  &__img{
    padding-top: 12.75em;
    img{
      width: 100%;
      min-height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__content{
    background-color: $yellow;
    padding: 14em 6.25em 6em;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  &__quote{
    position: absolute;
    width: 6.25em;
    height: 6.25em;
    background-color: $white;
    top: 0;
    left: 0;
    font-weight: 900;
    span{
      font-size: 7.5em;
      line-height: 120%;
      letter-spacing: -0.05em;
      margin-top: -.25em;
      display: block;
    }
  }
  &__text,
  &__author{
    font-size: 1.25em;
    line-height: 200%;
    text-transform: uppercase;
    letter-spacing: -0.05em;
  }
  &__text{
    min-height: 13em;
  }
  &__author{
    font-style: italic;
  }
}

.we-wait{
  padding: 3em 0 2.75em;
  .map{
    margin-top: 5.75em;
  }
}

.map{
  width: 100%;
  height: 31em;
}


@media screen and (max-width: 992px){
  .places{
    .container{
      padding: 0 1.25em;
    }
    &__top{
      padding: 0;
    }
    &__wood{
      display: none;
    }
    &__grid{
      padding: 0 1.125em;
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
    }
    .program-group{
      &__top{
        text-align: center;
      }
    }
  }

  .reviews{
    padding: .875em 0;
    .container{
      padding: 0 2.625em;
    }
    &__top{
      max-width: 40%;
    }
  }

  .reviews-slider{
    .swiper{
      &-button{
        width: 3.125em;
        height: 3.125em;
        img{
          width: 1.625em;
        }
      }
    }
  }

  .review-card{
    grid-template-columns: 50% 50%;
    &__img{
      padding-top: 7em;
    }
    &__content{
      padding: 4.75em 4.25em 3em 3em;
    }
    &__quote{
      width: 3.125em;
      height: 3.125em;
      @include center;
      top: -1px;
      left: -1px;
      span{
        margin: .25em 0 0;
        font-size: 3.75em;
      }
    }
    &__text,
    &__author{
      font-size: .75em;
    }
    &__text{
      min-height: 11em;
    }
  }

  .we-wait{
    padding: 1em 0 .875em;
    .map{
      margin-top: 2.25em;
    }
  }

  .map{
    height: 20em;
  }
}


@media screen and (max-width: 576px){
  .places{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .reviews{
    &__top{
      position: static;
      max-width: 100%;
    }
  }

  .reviews-slider{
    margin-top: 2em;
  }

  .review-card{
    grid-template-columns: 100%;
    &__img{
      padding: 0;
    }
  }
}
