@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 4em;
    min-width: 16.25em;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3em;
        min-width: 10em;
        span{
            font-size: .75em;
        }
    }
}
