.main{
  padding: 10em 0 3.125em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 3.75em;
    width: 100%;
    background-image: url(../img/pattern.png);
    background-size: auto 100%;
  }
  &__inner{
    position: relative;
  }
  &__grass{
    max-width: 100%;
    width: 62.75em;
    margin-left: auto;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__nav{
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__text{
    position: absolute;
    left: 0;
    top: 5em;
  }
  &__nav2{
    position: absolute;
    right: 14em;
    bottom: 10em;
  }
  &__title{
    background-color: $white;
    text-transform: uppercase;
    font-size: 1em;
    padding: 2em 3em 2em 0;
    span{
      display: block;
      font-size: 8.125em;
      line-height: 108%;
      font-weight: 900;
      letter-spacing: -0.075em;
      position: relative;
      @media screen and (min-width: 993px){
        &:before{
          content: '';
          position: absolute;
          background-color: $white;
          width: 1.25em;
          left: 100%;
          height: 1.346em;
          top: 50%;
          transform: translateY(-50%);
          display: none;
        }
        &:nth-child(2){
          &:before{
            display: block;
          }
        }
        &:nth-child(3){
          &:before{
            display: block;
            width: 3.5em;
            transform: unset;
            bottom: -.246em;
            top: unset;
          }
        }
      }
    }
  }
}

.invite{
  padding: 3.125em 0 4em;
  &__inner{
    max-width: 100%;
    width: 62.75em;
    margin-left: auto;
    position: relative;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      min-height: 17.625em;
      object-fit: cover;
    }
  }
  &__text{
    padding: 3.375em 2em .5em 3.375em;
    background-color: $white;
    width: 43.75em;
    max-width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;
      width: 1.875em;
      height: calc(100% + 2.5em);
      background-color: $primary;
    }
  }
  &__title{
    position: absolute;
    top: -2.5em;
    left: 0;
    padding: .5em 1.5em .5em 3.375em;
    background-color: $white;
    text-transform: uppercase;
    font-weight: 900;
    span{
      font-size: 1.875em;
      line-height: 120%;
    }
  }
  &__desc{
    font-size: 1.25em;
    line-height: 120%;
    letter-spacing: -0.05em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 5.875em 0 1em;
    &:before{
      background-image: url(../img/pattern_mob.png);
    }
    .container{
      padding: 0;
    }
    &__inner{
      padding: 7.25em 1em 6em;
    }
    &__grass{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      img{
        height: 100%;
        object-fit: cover;
      }
    }
    &__text,
    &__nav2{
      position: static;
      display: flex;
      justify-content: center;
    }
    &__nav2{
      margin-top: 1.875em;
    }
    &__title{
      background-color: transparent;
      padding: 0;
      text-align: center;
      @include grid100gap(.6875em);
      span{
        font-size: 2.625em;
        min-height: 1.33em;
        padding: .125em .5em;
        @include center;
        background-color: $white;
        letter-spacing: -0.025em;
      }
    }
    &__nav{
      top: unset;
      bottom: 100%;
      left: unset;
      right: 1em;
      margin-bottom: 2.5em;
    }
  }

  .invite{
    padding: 1em 0;
  }
}


@media screen and (max-width: 576px){
  .main{
    &__grass{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .invite{
    &__text{
      font-size: .54em;
    }
    &__desc{
      font-size: 1.4em;
    }
  }
}
