._grass-section{
  position: relative;
  &:after,
  &:before{
    content: '';
    position: absolute;
    top: 0;
    width: 3em;
    height: 100%;
    background-image: url(../img/grass.jpg);
    z-index: -1;
  }
  &:before{
    left: 0;
  }
  &:after{
    right: 0;
  }
}

.program{
  margin-top: 4em;
  position: relative;
  overflow: hidden;
  .container{
    padding-right: 5.5em;
    padding-left: 9.75em;
  }
  &__pattern{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__inner{
    padding-top: 6.25em;
    padding-bottom: 2em;
    display: grid;
    grid-template-columns: calc(100% - 27em) 19em;
    justify-content: space-between;
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: 28.125em auto;
    gap: 4.5em 4em;
  }
  &__bbq{
    position: absolute;
    left: .25em;
    top: 3.125em;
    width: 19.6875em;
    z-index: 2;
  }
  &__grill{
    position: absolute;
    right: 5.75em;
    bottom: 5em;
    width: 22.5em;
    z-index: 2;
  }
  &__bottom{
    margin: 3.5em 0 2em;
    max-width: 43em;
  }
  &__hint{
    font-size: 1.25em;
    line-height: 1.2;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: -0.05em;
  }
  &__leaf{
    position: absolute;
    top: 0;
    left: 19em;
    width: 10.375em;
  }
  &__leaf2{
    position: absolute;
    top: 21.5em;
    left: 28.25em;
    width: 2.5em;
  }
  &__leaf3{
    position: absolute;
    top: 17.75em;
    left: 46.875em;
    width: 4.25em;
  }
  &__leaf4{
    position: absolute;
    top: 40em;
    left: -5.5em;
    width: 4.625em;
  }
  &__leaf5{
    position: absolute;
    top: 61em;
    left: 20em;
    width: 3.375em;
  }
  &__leaf6,
  &__leaf7,
  &__leaf8,
  &__leaf9{
    position: absolute;
    display: none;
  }
}

.program-group{
  &__top{
    display: inline-flex;
    align-items: center;
    padding: 1em 1.5em;
    min-height: 4.625em;
    background-color: #f2d132;
    font-weight: 900;
    text-transform: uppercase;
    min-width: 15em;
  }
  &__title{
    font-size: 1.875em;
    line-height: 120%;
    letter-spacing: -0.05em;
  }
  &__bottom{
    padding: 2em 0 0 1.25em;
    @include grid100gap(1em);
  }
  &__item{
    &-desc,
    &-title{
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: -0.05em;
      text-transform: uppercase;
    }
  }
  &._type2{
    .program-group__item{
      display: grid;
      grid-template-columns: auto 4.5em;
      justify-content: space-between;
      gap: 1em;
    }
  }
}

.program-info{
  margin-top: -11.875em;
  &__top{
    background-color: $black;
    color: $white;
    padding: 2.375em 2.5em;
  }
  &__icon{
    width: 7.625em;
    display: block;
    margin-bottom: 2.75em;
  }
  &__bottom{
    padding: 1.25em 2.5em;
    background-color: $yellow;
  }
}


@media screen and (max-width: 992px){
  ._grass-section{
    &:after,
    &:before{
      width: 1em;
    }
  }

  .program{
    margin-top: 1em;
    .container{
      padding-right: 1.875em;
      padding-left: 1.875em;
    }
    &__pattern{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      padding-top: 7.375em;
      grid-template-columns: calc(100% - 9em) 8em;
      gap: 0 1em;
    }
    &__grid{
      grid-template-columns: 1.3fr 1fr;
      gap: 2em;
    }
    &__bbq{
      left: -1.25em;
      top: 3em;
      width: 10em;
    }
    &__grill{
      right: -6.25em;
      bottom: 14.5em;
      width: auto;
      height: 27em;
    }
    &__bottom{
      margin-top: 1.5em;
    }
    &__hint{
      font-size: 1em;
      span{
        color: #c81111;
        display: block;
        margin-bottom: 1em;
      }
      ._hide-mob{
        display: none;
      }
    }
    &__leaf{
      top: 5.5em;
      left: 9em;
      width: 4.375em;
    }
    &__leaf2{
      top: 16em;
      left: 12.5em;
      width: 1.125em;
    }
    &__leaf3{
      top: 14.5em;
      left: 20em;
      width: 1.75em;
    }
    &__leaf4{
      top: 25.5em;
      left: -1.875em;
      width: 1.875em;
    }
    &__leaf5{
      display: none;
    }
    &__leaf6{
      display: block;
      top: 36.25em;
      left: 10em;
      width: 1.5em;
    }
    &__leaf7{
      display: block;
      top: 53.75em;
      left: 5em;
      width: 2.625em;
    }
    &__leaf8{
      display: block;
      top: 58em;
      left: 8em;
      width: .75em;
    }
    &__leaf9{
      display: block;
      top: 59.5em;
      left: 0;
      width: 1.25em;
    }
  }

  .program-group{
    &__top{
      padding: .5em 1em;
      min-height: 2.625em;
      min-width: unset;
    }
    &__title{
      font-size: 1em;
    }
    &__bottom{
      padding: 1.5em 0 0 1em;
      gap: .5em;
    }
    &__item{
      &-desc,
      &-title{
        font-size: .75em;
      }
    }
    &._type2{
      //.program-group__item{
      //  display: grid;
      //  grid-template-columns: auto 4.5em;
      //  justify-content: space-between;
      //}
    }
  }

  .program-info{
    &__top{
      padding: 1.625em 1em .5em;
    }
    &__icon{
      width: 3.1875em;
      margin-bottom: .75em;
    }
    &__bottom{
      padding: .5em 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .program{
    &__inner,
    &__grid{
      grid-template-columns: 100%;
    }
    &__inner{
      position: relative;
    }
  }

  .program-group{
    &:nth-child(2){
      order: 2;
    }
  }

  .program-info{
    margin: 0;
    position: absolute;
    right: 0;
    top: -5.625em;
    width: 8em;
    z-index: 2;
  }
}
