.about{
  padding: 7em 0 5.625em;
  position: relative;
  &:before{
    z-index: 2;
  }
  .container{
    padding-left: 8.125em;
    padding-right: 4.75em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 29em) 21.5em;
    justify-content: space-between;
  }
  .text{
    margin-top: 3em;
  }
  &__hot-dog{
    position: absolute;
    top: -2.75em;
    left: 0;
    z-index: 3;
    width: 18.25em;
  }
  .program-group__bottom{
    padding-right: 1.25em;
  }
  .program-group__item-title{
    line-height: 200%;
  }
}

.text{
  p{
    font-size: 1.25em;
    line-height: 1.2;
    letter-spacing: -0.05em;
    margin-bottom: 1em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  b{
    font-weight: 900;
  }
  ._bg{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: calc(100% + 1em);
      right: 0;
      top: 50%;
      width: 200vw;
      z-index: -1;
      transform: translateY(-50%);
      background-color: $yellow;
    }
  }
}

.catalog{
  padding: 5.625em 0 0;
  position: relative;
  .container{
    padding-left: 6.25em;
    padding-right: 5.625em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 38.625em calc(100% - 38.625em - 7.5em);
    justify-content: space-between;
    padding-bottom: 6.25em;
  }
  &__img{
    padding-top: 3.75em;
    padding-left: 3.5em;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__rect{
    position: absolute;
    width: 6.25em;
    height: 6.25em;
    background-color: $white;
  }
  &__rect1{
    right: 0;
    bottom: 0;
  }
  &__rect2{
    right: 0;
    bottom: 6.25em;
  }
  &__rect3{
    bottom: 0;
    right: 6.25em;
  }
  &__rect4{
    z-index: -1;
    background-color: $primary;
    width: 15em;
    height: 15em;
    top: 0;
    left: 0;
  }
  &__items{
    @include grid100gap(2.5em);
  }
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-left: 5.25em;
    padding-top: 1.5em;
  }
  &__veg{
    position: absolute;
    width: 20.25em;
    top: -7.5em;
    right: 1.25em;
  }
  .program-group{
    display: none;
  }
}

.catalog-item{
  display: flex;
  &__icon{
    flex-shrink: 0;
    background-color: $yellow;
    padding: .125em;
    @include center;
    width: 3.75em;
    height: 3.75em;
    margin-right: 1.5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    font-size: 1.25em;
    line-height: 120%;
    letter-spacing: -0.05em;
    margin-top: .4em;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 1.875em 0 2.25em;
    .container{
      padding: 0 2em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      display: none;
    }
    .text{
      margin-top: 1.25em;
    }
    &__hot-dog{
      top: -3.375em;
      width: 10.5em;
    }
  }

  .text{
    p{
      font-size: 1em;
    }
  }

  .catalog{
    padding: 0;
    .container{
      padding-left: 1.75em;
      padding-right: 1.75em;
    }
    &__grid{
      grid-template-columns: 20em calc(100% - 21em);
      gap: 2em 1em;
      padding-bottom: 1.875em;
    }
    &__img{
      padding-top: 2em;
      padding-left: 2em;
      position: relative;
    }
    &__rect{
      display: none;
    }
    &__rect4{
      display: block;
      width: 8.25em;
      height: 8.25em;
    }
    &__items{
      gap: .5em;
    }
    &__nav{
      padding-left: 3.25em;
    }
    &__veg{
      width: 9.5em;
      top: unset;
      bottom: -.875em;
      right: 0;
    }
  }

  .catalog-item{
    &__icon{
      width: 2em;
      height: 2em;
      margin-right: 1.25em;
    }
    &__desc{
      font-size: .75em;
      margin-top: .2em;
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    p{
      font-size: .75em;
    }
  }

  .catalog{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 0 1em;
    }
    &__nav{
      padding-left: 0;
      justify-content: center;
    }
    .program-group{
      display: block;
      padding-bottom: 1em;
      margin-left: -.75em;
      &__bottom{
        max-width: 13em;
        padding-top: .75em;
      }
    }
  }
}
